import './NavBar.css';
import { useEffect } from 'react';

function NavBar(){

  const list = {
    'Home': 'home',
    'About me': 'about-me',
    'Projects': 'projects',
    'Contact me': 'contact-me'
  };

  useEffect(()=>{

    let nav_list = document.querySelector('#nav-list'),
    menu_btn = document.querySelector('#menu-btn'),
    html_ele = document.querySelector('html');

    const showNavList = () => {

      html_ele.classList.add('stop-scrolling');
      menu_btn.children[0].classList.remove("os-20_menu-3");
      menu_btn.children[0].classList.add("os-20_close");
      nav_list.classList.add('show');

    };

    const hideNavList = () => {

      menu_btn.children[0].classList.remove("os-20_close");
      menu_btn.children[0].classList.add("os-20_menu-3");
      nav_list.classList.remove('show');
      html_ele.classList.remove('stop-scrolling');

    };

    document.querySelector('#nav-list > li[name="home"]').classList.add('active');

    document.querySelectorAll('#nav-list > li').forEach(li => {

      li.onclick = () => {

        let name = li.getAttribute('name'),
        page_offset_top = document.querySelector(`section.${name}`).offsetTop;

        window.scrollTo(0, page_offset_top);

        hideNavList();

      }

    });

    menu_btn.onclick = function(ele){

      if(nav_list.className.includes('show')) hideNavList();
      else showNavList();

    };

    document.onscroll = () => {

      let window_height = window.innerHeight - 64;

      Object.values(list).forEach(e=>{

        let ele = document.querySelector(`section.${e}`),
        offset_top = ele.offsetTop,
        offset_height = ele.offsetHeight,
        li = document.querySelector(`#nav-list > li[name="${e}"]`),
        scroll_y = window_height + window.scrollY;

        if(offset_top <= scroll_y && offset_height + offset_top >= scroll_y){
          li.classList.add('active');
        }else{
          li.classList.remove('active');
        }
        
      });

    };

  }, []);

  return (
    <nav className='nav-bar' >
      <ul id='nav-list' className='nav-list' >
        {Object.entries(list).map((l, i)=>{
          return (
            <li key={i} name={l[1]} >{l[0]}</li>
          )
        })}
      </ul>
      <button aria-label='menu' id='menu-btn' >
        <span className='remixicon os-20_menu-3' ></span>
      </button>
    </nav>
  )

}

export default NavBar;