import './Preloader.css';
import { useEffect } from 'react';

function Preloader(){

  useEffect(()=>{

    window.onload = () =>{
      
      let ele = document.querySelector('#preloader');

      ele.children[0].classList.remove('blinking');
      ele.children[0].classList.add('anim-run');

      window.setTimeout(()=>{

        ele.remove();

      }, 1000);

    };

  }, []);

  return (
    <div id='preloader' className='preloader' >
      <span className='loader blinking' ></span>
    </div>
  )

}

export default Preloader;