import './AboutMe.css';

function AboutMe(){

  const frontend_skill = {
    'HTML': 'Proficient',
    'CSS': 'Proficient',
    'JavaScript': 'Skillfull',
    'jQuery': 'Skillfull',
    'React.js': 'Beginner',
    'Responsive Design': 'Proficient'
  };

  return (
    <section className='about-me' >
      <h2 className='h2 shape-rect' >About <br/><span>Me</span></h2>
      <p>
        I'm <em>front-end web developer</em> based in Uttar Pradesh. I'm passionate about creating <em>beautiful</em> + <em>interactive</em> + <em>dynamic</em> + <em>responsive</em> user-friendly websites that not only look great but also provide an excellent <em>user experience</em>.
        <br/><br/>
        I also build API + CRUD application using PHP + MySQL. In my free time, I enjoy doing some <em>graphic designing</em>.
      </p>
      <h3 className='h3 shape-line' >Technical <span>Skills</span></h3>
      <section className='skills-box' >
        {Object.entries(frontend_skill).map((s, i)=>{
          return (
            <div key={i} >
              <span>{s[0]}</span>
              <span>{s[1]}</span>
            </div>
          )
        })}
      </section>
    </section>
  )

}

export default AboutMe;